* {
  overscroll-behavior: none;
  overscroll-behavior-block: none;
  margin: 0;
  padding: 0;
}

body {
  max-height: 1864px;
  grid-template-columns: repeat(12, 1fr);
  font-family: Poppins;
}

.home, .contact, .five-year, .products-body {
  background-blend-mode: screen;
  background-color: #889696;
  background-image: url("hero-image-min-min.55a28716.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  grid-template-columns: repeat(12, 1fr);
  display: grid;
}

a {
  color: #000;
  text-decoration: none;
}

header {
  height: 100px;
  grid-column: span 12;
  grid-template-columns: repeat(12, 1fr);
  align-items: center;
  padding: 10px;
  display: grid;
}

header nav {
  grid-column: 5 / span 7;
  justify-content: space-between;
  padding: 10px;
  font-size: 2em;
  display: flex;
}

nav a {
  color: #333;
  justify-self: end;
  font-weight: 800;
  text-decoration: none;
}

.main {
  gap: 20dppx;
  grid-column: 1 / span 12;
  grid-template-columns: repeat(12, 1fr);
  display: grid;
}

.welcome {
  width: 100%;
  grid-column: 4 / span 9;
  margin-top: 30px;
  font-size: 1.5em;
  font-weight: 800;
  line-height: 1.5em;
}

.welcome h1 {
  width: 100%;
  margin-top: 50px;
  margin-bottom: .5em;
  font-family: Poppins;
  font-size: 4em;
  font-weight: 800;
}

.welcome h2 {
  margin-bottom: 40px;
}

.welcome p {
  max-width: 860px;
}

.welcome img {
  filter: saturate(50%);
  margin-bottom: 20px;
}

nav a:hover {
  color: #fff;
  text-shadow: 2px 2px 2px #00000080;
}

.slider-row {
  width: 100%;
  height: 420px;
  grid-column: span 12;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  margin-top: 30px;
  margin-bottom: 30px;
  display: grid;
}

.slide-button {
  width: 50px;
  height: 60px;
  background: none;
  border: #0000;
  font-size: 4em;
  font-weight: 800;
}

.slide-button :hover {
  color: #fff;
}

.previous {
  position: absolute;
  bottom: 45%;
}

.next {
  position: absolute;
  bottom: 45%;
  right: 15px;
}

.slider {
  width: 700px;
  height: 420px;
  grid-column: 2 / span 1;
  position: relative;
  overflow: hidden;
}

.slide {
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: 0;
  justify-content: center;
  align-items: center;
  transition: opacity 3s ease-in;
  display: flex;
  position: absolute;
  top: 0;
}

.slide > img {
  object-fit: cover;
  height: 100%;
  width: 100%;
}

.contact-button {
  width: 200px;
  height: 50px;
  border-radius: 6px;
  font-weight: 800;
  box-shadow: 4px 4px 4px #0009;
}

.contact button:hover {
  filter: brightness(1.8);
  box-shadow: 1px 1px 1px #0000004d;
}

.warranty {
  min-width: 100%;
  opacity: .8;
  background-color: #ddd;
  border-radius: 6px;
  grid-column: 2 / span 9;
  grid-template-columns: 1fr;
  justify-content: flex-start;
  margin-top: 50px;
  padding: 30px 60px;
  display: grid;
}

.warranty h1 {
  opacity: 1;
  grid-column: span 1;
  justify-self: center;
  font-size: 2em;
}

.warranty h3 {
  font-style: bold;
  align-self: flex-start;
}

.warranty p {
  margin-top: 10px;
  margin-bottom: 10px;
}

h5 {
  font-style: italic;
}

form {
  height: 600px;
  opacity: .8;
  box-sizing: border-box;
  background-color: #ddd;
  border-radius: 6px;
  grid-column: 2 / span 10;
  grid-template-columns: repeat(12, 1fr);
  margin: 0 auto;
  padding: 25px;
  display: grid;
  position: relative;
  top: 50px;
  bottom: 450px;
}

form input {
  height: 30px;
  background-color: #fff;
  border-bottom: 2px solid #000;
  border-radius: 6px;
  grid-column: 6 / span 6;
  padding: 5px;
}

#message {
  height: 120px;
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 6px;
  grid-column: 6 / span 6;
  padding: 5px;
}

#contact-info {
  width: 100%;
  grid-column: 1 / span 4;
  font-weight: 800;
  position: absolute;
  top: 10px;
}

#contact-info p {
  margin-top: 20px;
  line-height: 1em;
}

form button {
  grid-column: 8 / span 2;
}

.history {
  grid-template-columns: 1.2fr 1fr 1fr;
  margin: 0;
  padding: 0;
  font-family: Poppins;
  display: grid;
}

.panel {
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.panel.introduction {
  background-color: #323230;
  padding: 30px 60px;
}

.panel.introduction h1 {
  color: #f5f5f5;
  font-size: 4em;
}

.panel.introduction a {
  color: #a8a8a8;
  margin-right: 10px;
  font-size: 1.55em;
}

.panel.introduction a:hover {
  color: #fff;
  text-shadow: 2px 2px 2px #00000080;
}

.panel.introduction p {
  color: #a8a8a8;
  max-width: 540px;
  line-height: 2em;
}

.panel.introduction footer {
  color: #a8a8a8;
  align-self: end;
}

.panel.about {
  grid-template-rows: .8fr 4fr;
  overflow: hidden;
}

.panel.about img {
  min-width: 100%;
  grid-row: span 1;
}

.panel.leadership {
  background-color: #eeeee7;
  grid-template-rows: 1fr .8fr .8fr;
  padding: 0;
  overflow: hidden;
}

.panel.leadership img {
  min-width: 100%;
  max-width: 100%;
}

.text {
  max-width: 100%;
  max-height: 100%;
  color: #323230;
  grid-row: span 1;
  padding: 10px;
}

.products-main {
  width: 100%;
  max-height: 1300px;
  grid-column: span 12;
  grid-template-columns: 1fr;
  grid-auto-rows: 1fr;
  display: grid;
}

.products-row.one {
  max-width: 100%;
  grid-area: 1 / span 1 / span 1;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 1fr;
  align-items: center;
  margin-top: 30px;
  display: grid;
  position: relative;
}

.products-row.two {
  max-width: 100%;
  grid-row: 2 / span 2;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 1fr;
  display: grid;
}

.products-card {
  box-sizing: border-box;
  max-width: 100%;
  flex-direction: column;
  grid-area: span 1 / span 1;
  margin: 0;
  padding: 0;
  display: flex;
  position: relative;
}

.products-card img {
  object-fit: cover;
  z-index: -1;
  max-width: 100%;
  max-height: 100%;
  grid-column: span 1;
}

.products-card a {
  color: #fff;
  text-shadow: 2px 2px 2px #0000004d;
  align-self: center;
  font-size: 2.5em;
  font-weight: 800;
  text-decoration: none;
  position: absolute;
  top: 190px;
}

.products-card a:hover {
  text-shadow: 2px 2px 2px #fff;
  color: #000;
}

.products-header {
  grid-row: 1 span 1;
  grid-template-columns: repeat(12 1fr);
  height: 0;
  display: grid;
  position: fixed;
}

.products-header nav {
  grid-column: span 12;
  justify-self: start;
}

.products-header nav a {
  color: #000;
  margin-right: 30px;
  font-size: 1em;
}

.products-header nav a:hover {
  color: #fff;
  text-shadow: 2px 2px 2px #00000080;
}

.drop-down-menu {
  max-height: 800px;
  width: 100%;
  box-sizing: border-box;
  grid-area: 1 / 1 / span 1 / span 4;
  grid-template-columns: repeat(6, 1fr);
  grid-auto-rows: 1fr;
  justify-content: center;
  place-self: start;
  gap: 50px;
  margin: 0;
  padding: 20px;
  font-size: 1em;
  font-weight: 800;
  transition: height .7s, all ease-in, width .7s ease-in, font-size .7s ease-in;
  display: grid;
  position: absolute;
}

.hidden {
  height: 0;
  width: 0;
  font-size: 0;
}

.menu-button {
  height: 50px;
  width: 100px;
  box-shadow: none;
  opacity: .5;
  background-color: #0000;
  border: none;
  font-size: 4em;
  transition: height .7s, all ease-in, width .7s ease-in, font-size .7s ease-in;
}

.menu-button:hover {
  color: #fff;
  opacity: 1;
  box-shadow: none;
}

.hidden-button {
  width: 0;
  height: 0;
  opacity: 0;
  font-size: 0;
}

.menu-cell {
  text-align: center;
  width: 200px;
  height: 200px;
  grid-area: span 1 / span 2;
  grid-template-columns: 1fr;
  grid-auto-rows: 1fr;
  transition: height .7s, all ease-in, width .7s ease-in, font-size .7s ease-in;
}

.product-image {
  grid-area: span 1 / span 2;
  transition: height .7s, all ease-in, width .7s ease-in, font-size .7s ease-in;
}

.product-paragraph {
  max-height: 100%;
  max-width: 100%;
  grid-column: 1 / span 3;
  font-size: 1.25em;
  font-weight: 800;
  line-height: 1.25em;
  transition: height .7s, all ease-in, width .7s ease-in, font-size .7s ease-in;
}

.drop-down-menu ul {
  max-width: 100%;
  max-height: 100%;
  grid-column: span 3;
  transition: height .7s, all ease-in, width .7s ease-in, font-size .7s ease-in;
}

@media screen and (max-width: 1279px) {
  .welcome {
    margin-top: 20px;
  }

  .welcome h1 {
    font-size: 3em;
  }

  .welcome img {
    height: 100px;
    width: 100px;
  }

  header nav {
    grid-column: 1 / span 12;
  }

  .panel.introduction nav {
    font-size: .7em;
  }

  .panel.introduction h1 {
    font-size: 3em;
  }

  .products-card a {
    position: absolute;
    top: 150px;
  }
}

@media screen and (max-width: 992px), screen and (orientation: portrait) {
  .welcome h1 {
    font-size: 2.5em;
  }

  .welcome img {
    height: 80px;
    width: 80px;
  }

  .products-card {
    font-size: .8em;
  }

  .products-card a {
    position: absolute;
    top: 130px;
  }

  header nav {
    grid-column: 1 / span 12;
    font-size: 1.5em;
  }

  .panel.introduction nav {
    height: 0;
    flex-direction: column;
    display: flex;
  }

  .panel.introduction {
    justify-content: space-between;
  }

  form {
    grid-column: span 12;
  }

  form input, #message {
    grid-column: 7 / span 5;
  }

  .drop-down-menu ul {
    grid-column: span 4;
  }
}

@media screen and (max-width: 768px), screen and (orientation: portrait) {
  .welcome h1 {
    font-size: 2em;
  }

  .products-card {
    grid-area: span 1 / span 2;
  }

  .product-paragraph {
    grid-column: span 5;
  }

  .products-card a {
    position: absolute;
    top: 170px;
  }

  .menu-cell {
    width: 200px;
    height: 100px;
  }

  .history {
    grid-template-columns: 1fr 1fr;
    display: grid;
  }

  .panel.introduction, .panel.about {
    grid-column: span 1;
  }

  .panel.leadership {
    grid-column: span 2;
  }

  form {
    padding: 10px;
  }

  .warranty {
    grid-column: 1.5 / span 11;
    padding: 15px;
    font-size: .8em;
  }

  .warranty p {
    max-width: 100%;
    grid-column: span 12;
    justify-self: center;
  }

  .slider {
    width: 500px;
    height: 300px;
  }
}

@media screen and (max-width: 576px) {
  header nav a {
    font-size: .8em;
  }

  .panel.introduction, .panel.about {
    grid-column: span 2;
  }

  .panel.introduction nav {
    height: 150px;
  }

  .panel.introduction footer {
    margin-top: 40px;
  }

  .welcome {
    grid-column: 2 / span 12;
    line-height: 1.25;
  }

  .welcome h1 {
    font-size: 2em;
  }

  .welcome h2 {
    font-size: 1.4em;
  }

  .welcome img {
    height: 60px;
    width: 60px;
  }

  .home header nav, .five-year header nav, .contact header nav, .products-body header nav {
    height: 110px;
    grid-template-columns: 1fr 1fr;
    justify-self: start;
    font-size: 1.5em;
    display: grid;
  }

  .home header nav a, .five-year header nav a, .contact header nav a, .products-body header nav a {
    grid-column: 1 / span 2;
    justify-self: start;
    margin-right: 10px;
  }

  form {
    height: 550px;
    margin-top: 50px;
  }

  form input, #message {
    grid-column: 8 / span 5;
  }

  .drop-down-menu {
    gap: 30px;
  }

  .menu-cell {
    width: 150px;
    height: 75px;
  }

  .menu-button {
    margin-top: 40px;
  }

  .products-card a {
    position: absolute;
    top: 125px;
  }

  .slider {
    width: 400px;
    height: 280px;
  }

  .drop-down-menu ul {
    grid-column: span 6;
  }
}

@media screen and (max-width: 390px) {
  .products-card {
    grid-column: span 4;
  }

  .product-paragraph {
    grid-column: span 6;
    font-size: 1em;
  }

  .menu-cell {
    width: 125px;
    height: 62px;
    grid-column: span 3;
  }

  .welcome img {
    height: 40px;
    width: 40px;
  }

  .welcome h1 {
    font-size: 1.5em;
  }

  #contact-info {
    grid-column: 1 / span 12;
    position: relative;
  }

  form input, #message {
    grid-column: 1 / span 12;
    margin-top: 10px;
  }

  form button {
    grid-column: 5 / span 4;
  }

  .products-card a {
    position: absolute;
    top: 200px;
  }

  .slider {
    width: 380px;
    height: 230px;
  }
}

